import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from 'src/app/shared/service/api.service';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { environment } from 'src/environments/environment';


export interface IChannelCD {
  id?: string;
  name: string;
}

@Component({
  selector: 'app-channel-cd',
  templateUrl: './channel-cd.component.html',
  styleUrls: ['./channel-cd.component.scss']
})
export class ChannelCdComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;
  private entity = 'channel';
  channelCDs: IChannelCD[] = [];
  private application: any = this.localDataService.get('application');

  staticCenters: IChannelCD[] = [
    {
      id: '1',
      name: 'Channel A',
    },
    {
      id: '2',
      name: 'Channel B',
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private localDataService: LocalDataService
  ) { }

  ngOnInit(): void {
    this.buildForm();

    // TEMP
    if ( environment.staticData ) {
      this.channelCDs = this.staticCenters;
      return;
    }

    const endpoint = `${this.entity}/getAllChannelCdByAppId`;
    this.apiService.list(endpoint, {
      appId: this.application.id
    }).subscribe( x => {
      this.channelCDs = x.channelCds;
    });
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]]
    });
  }

  showAppForm(type: string, center?: IChannelCD): void {
    if (type === 'add') {
      this.modalTitle = 'New Channel CD';
    } else {
      this.modalTitle = `Edit ${center.name}`;
      this.form.setValue({
        id: center.id,
        name: center.name
      });
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name }: IChannelCD = this.form.value;

    if (id) {
      this.update({id, name});
    } else {
      this.create({name});
    }

    this.form.reset();
  }

  private create({ name }): void{

    // TEMP
    if (environment.staticData) {
      this.channelCDs.push({
        id: new Date().getTime().toString(),
        name
      });
      return;
    }

    this.apiService.create(`${this.entity}/saveChannelCd` , {
      applicationId: this.application.id,
      channelCdValue: name
    }).subscribe ( x => {
        this.channelCDs.push({
          id: x.id,
          name
        });
    });
  }

  private update({id, name}): void{
    // TEMP
    if (environment.staticData) {
      this.channelCDs.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
      return;
    }

    this.apiService.create(`${this.entity}/updateChannelCd`, {
      channelCdValue: name,
      channelCd: id,
    }).subscribe ( x => {
      this.channelCDs.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
    });
  }

  deleteApp(app: IChannelCD): void {
    const sure = confirm('Are you sure?');
    if (sure) {

      // TEMP
      if (environment.staticData) {
        this.channelCDs = this.channelCDs.filter( x => x.id !== app.id);
        return;
      }

      this.apiService.delete2(`${this.entity}/deleteChannelCd`, app.id).subscribe( x => {
        this.channelCDs = this.channelCDs.filter( o => o.id !== app.id);
      });
    }
  }

}
