import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from 'src/app/shared/service/api.service';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { environment } from 'src/environments/environment';

export interface ITeam {
  id?: string;
  name: string;
}

@Component({
  selector: 'app-team-list',
  templateUrl: './team-list.component.html',
  styleUrls: ['./team-list.component.scss']
})
export class TeamListComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;
  private entity = 'team-shop';
  private application: any = this.localDataService.get('application');

  teams: ITeam[] = [];
  staticTeams: ITeam[] = [
    {
      id: '1',
      name: 'Team A',
    },
    {
      id: '2',
      name: 'Team B',
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private localDataService: LocalDataService
  ) { }

  ngOnInit(): void {
    this.buildForm();

    // TEMP
    if ( environment.staticData ) {
      this.teams = this.staticTeams;
      return;
    }

    const endpoint = `${this.entity}/find-by-application/${this.application.id}`;
    this.apiService.list(endpoint).subscribe( x => {
      this.teams = x;
    });
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]],
    });
  }

  showAppForm(type: string, team?: ITeam): void {
    if (type === 'add') {
      this.modalTitle = 'New Team';
    } else {
      this.modalTitle = `Edit ${team.name}`;
      this.form.setValue({
        id: team.id,
        name: team.name
      });
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name }: ITeam = this.form.value;

    if (id) {
      this.update({id, name});
    } else {
      this.create({name});
    }

    this.form.reset();
  }

  private create({ name }): void{

    // TEMP
    if (environment.staticData) {
      this.teams.push({
        id: new Date().getTime().toString(),
        name
      });
      return;
    }

    this.apiService.create(this.entity, {
      application_id: this.application.id,
      name
    }).subscribe ( x => {
        this.teams.push({
          id: x.id,
          name
        });
    });
  }

  private update({id, name}): void{
    // TEMP
    if (environment.staticData) {
      this.teams.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
      return;
    }

    this.apiService.update(this.entity, id, {
      name,
      applicationId: this.application.id,
    }).subscribe ( x => {
      this.teams.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
    });
  }

  deleteApp(app: ITeam): void {
    const sure = confirm('Are you sure?');
    if (sure) {

      // TEMP
      if (environment.staticData) {
        this.teams = this.teams.filter( x => x.id !== app.id);
        return;
      }

      this.apiService.delete(this.entity, app.id).subscribe( x => {
        this.teams = this.teams.filter( o => o.id !== app.id);
      });
    }
  }

}
