import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from 'src/app/shared/service/api.service';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { environment } from 'src/environments/environment';

export interface ICenter {
  id?: string;
  name: string;
}

@Component({
  selector: 'app-center-list',
  templateUrl: './center-list.component.html',
  styleUrls: ['./center-list.component.scss']
})
export class CenterListComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;
  private entity = 'center-cd';
  centers: ICenter[] = [];
  private application: any = this.localDataService.get('application');

  staticCenters: ICenter[] = [
    {
      id: '1',
      name: 'Channel A',
    },
    {
      id: '2',
      name: 'Channel B',
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private localDataService: LocalDataService
  ) { }

  ngOnInit(): void {
    this.buildForm();

    // TEMP
    if ( environment.staticData ) {
      this.centers = this.staticCenters;
      return;
    }

    const endpoint = `${this.entity}/fetch-by-application`;
    this.apiService.list(endpoint,
      {
        application_id: this.application.id,
        limit: 99,
        offset: 0
      }).subscribe( x => {
      this.centers = x;
    });
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]]
    });
  }

  showAppForm(type: string, center?: ICenter): void {
    if (type === 'add') {
      this.modalTitle = 'New Center';
    } else {
      this.modalTitle = `Edit ${center.name}`;
      this.form.setValue({
        id: center.id,
        name: center.name
      });
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name }: ICenter = this.form.value;

    if (id) {
      this.update({id, name});
    } else {
      this.create({name});
    }

    this.form.reset();
  }

  private create({ name }): void{

    // TEMP
    if (environment.staticData) {
      this.centers.push({
        id: new Date().getTime().toString(),
        name
      });
      return;
    }

    this.apiService.create(this.entity, {
      application_id: this.application.id,
      name
    }).subscribe ( x => {
        this.centers.push({
          id: x.id,
          name
        });
    });
  }

  private update({id, name}): void{
    // TEMP
    if (environment.staticData) {
      this.centers.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
      return;
    }

    this.apiService.update(this.entity, id, {
      name,
      applicationId: this.application.id,
    }).subscribe ( x => {
      this.centers.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
    });
  }

  deleteApp(app: ICenter): void {
    const sure = confirm('Are you sure?');
    if (sure) {

      // TEMP
      if (environment.staticData) {
        this.centers = this.centers.filter( x => x.id !== app.id);
        return;
      }


      this.apiService.delete(this.entity, app.id).subscribe( x => {
        this.centers = this.centers.filter( o => o.id !== app.id);
      });
    }
  }

}
