import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/shared/service/api.service';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { environment } from 'src/environments/environment';


export interface IApplication  {
  id: string;
  name: string;
  shortname?: string;
  description?: string;
}

@Component({
  selector: 'app-application',
  templateUrl: './application.component.html',
  styleUrls: ['./application.component.scss']
})
export class ApplicationComponent implements OnInit {

  public entity = 'application';
  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;
  public apps: IApplication[] = [];
  public staticApps: IApplication[] = [
    {
      id: '1',
      name: 'SVC',
      shortname: 'SVC',
      description: 'Lorem ipsum dolor sit amet consectetur, adipisicing'
    },
    {
      id: '2',
      name: 'BOM',
      shortname: 'BOM',
      description: 'Lorem ipsum dolor sit amet consectetur, adipisicing'
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private router: Router,
    private localDataService: LocalDataService
  ) { }

  ngOnInit(): void {
    this.buildForm();

    // TEMP
    if (environment.staticData) {
      this.apps = this.staticApps;
      return;
    }

    this.apiService.list('application', {limit: 100, offset: 0}).subscribe( x => {

      this.apps = x;
      console.log(this.apps);
    });
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]],
      shortname: ['', [Validators.required]],
      description: ''
    });
  }

  showAppForm(type: string, app?: IApplication): void {
    if (type === 'add') {
      this.modalTitle = 'New Application';
    } else {
      this.modalTitle = `Edit ${app.name}`;
      this.form.setValue(app);
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name, shortname, description}: IApplication = this.form.value;

    if (id) {
      this.update({id, name, shortname, description});
    } else {
      this.create({ name, shortname, description});
    }

    this.form.reset();
  }

  deleteApp(app: IApplication): void {
    const sure = confirm('Are you sure?');
    if (sure) {

      // TEMP
      if (environment.staticData) {
        this.apps = this.apps.filter( o => o.id !== app.id);
        return;
      }

      this.apiService.delete(this.entity, app.id).subscribe( x => {
        this.apps = this.apps.filter( o => o.id !== app.id);
      });
    }
  }


  private update({id, name, shortname, description}): void{
    // TEMP
    if (environment.staticData) {

      this.apps.map( x => {
        if (id === x.id) {
          x.name = name;
          x.shortname = shortname;
          x.description = description;
        }
      });

      return;
    }


    this.apiService.update(this.entity, id, {
      description,
      shortname,
      name
    }).subscribe ( x => {
      this.apps.map( o => {
        if (id === o.id) {
          o.name = name;
          o.shortname = shortname;
          o.description = description;
        }
      });
    });
  }

  private create({ name, shortname, description }): void{

    // TEMP
    if (environment.staticData) {
      this.apps.push({
        id: new Date().getTime().toString(),
        name,
        shortname,
        description
      });
      return;
    }

    this.apiService.create(this.entity, {
      description,
      shortname,
      name
    }).subscribe ( x => {
        this.apps.push({
          id: x.id,
          name,
          shortname,
          description
        });
    });
  }

  selectApp(app: IApplication): void {
    this.localDataService.save('application', app);
    this.router.navigate(['/applications/', app.id]);
  }
}
