import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from 'src/app/shared/service/api.service';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { environment } from 'src/environments/environment';

export interface IFunctionGroup {
  id?: string;
  name: string;
}

@Component({
  selector: 'app-function-group-list',
  templateUrl: './function-group-list.component.html',
  styleUrls: ['./function-group-list.component.scss']
})
export class FunctionGroupListComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;
  private entity = 'function-group';
  private application: any = this.localDataService.get('application');

  functionGroups: IFunctionGroup[];
  staticFunctionGroups: IFunctionGroup[] = [
    {
      id: '1',
      name: 'PTC-Manager',
    },
    {
      id: '2',
      name: 'IBS-Frontline',
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private localDataService: LocalDataService
  ) { }

  ngOnInit(): void {
    this.buildForm();

    // TEMP
    if ( environment.staticData ) {
      this.functionGroups = this.staticFunctionGroups;
      return;
    }

    const endpoint = `${this.entity}/fetch-by-application`;
    this.apiService.list(endpoint, {
      application_id: this.application.id,
      limit: 999,
      offset: 0
    }).subscribe( x => {
      this.functionGroups = x;
    });
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]],
    });
  }

  showAppForm(type: string, functionGroup?: IFunctionGroup): void {
    if (type === 'add') {
      this.modalTitle = 'New Function Group';
    } else {
      this.modalTitle = `Edit ${functionGroup.name}`;
      this.form.setValue({
        id: functionGroup.id,
        name: functionGroup.name
      });
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name }: IFunctionGroup = this.form.value;

    if (id) {
      this.update({id, name});
    } else {
      this.create({name});
    }

    this.form.reset();
  }

  private create({ name }): void{

    // TEMP
    if (environment.staticData) {
      this.functionGroups.push({
        id: new Date().getTime().toString(),
        name
      });
      return;
    }

    this.apiService.create(this.entity, {
      application_id: this.application.id,
      name
    }).subscribe ( x => {
        this.functionGroups.push({
          id: x.id,
          name
        });
    });
  }

  private update({id, name}): void{
    // TEMP
    if (environment.staticData) {
      this.functionGroups.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
      return;
    }

    this.apiService.update(this.entity, id, {
      name,
      applicationId: this.application.id,
    }).subscribe ( x => {
      this.functionGroups.map( o => {
        if (id === o.id) {
          o.name = name;
        }
      });
    });
  }

  deleteApp(app: IFunctionGroup): void {
    const sure = confirm('Are you sure?');
    if (sure) {

      // TEMP
      if (environment.staticData) {
        this.functionGroups = this.functionGroups.filter( x => x.id !== app.id);
        return;
      }


      this.apiService.delete(this.entity, app.id).subscribe( x => {
        this.functionGroups = this.functionGroups.filter( o => o.id !== app.id);
      });
    }
  }

}
