import { Component, OnInit } from '@angular/core';
import { ITab } from 'src/app/shared/components/tab/tab.interface';
import { LocalDataService } from 'src/app/shared/service/local-data.service';
import { IApplication } from '../application/application.component';

@Component({
  selector: 'app-application-view',
  templateUrl: './application-view.component.html',
  styleUrls: ['./application-view.component.scss']
})
export class ApplicationViewComponent implements OnInit {

  tabItems: ITab[] = [
    // {
    //   id: 'api',
    //   label: 'API'
    // },
    {
      id: 'function_group',
      label: 'Function Groups'
    },
    {
      id: 'center',
      label: 'Centers CD'
    },
    // {
    //   id: 'team',
    //   label: 'Teams'
    // },
    {
      id: 'channel_cd',
      label: 'Channel CD'
    },
    {
      id: 'channel_id',
      label: 'Channel ID'
    },
  ];
  activeTab = this.tabItems[0];
  application: any;

  constructor(
    private localDataService: LocalDataService
  ) { }

  ngOnInit(): void {
    this.application = this.localDataService.get('application');
  }

  tabSelect(tab: ITab): void{
    this.activeTab = tab;
  }

}
